<template>
  <div id="app" @wheel="wheel">
    <div class="disabled">
      <h1>
        Please, open please use a PC to fully and correctly display all elements
        and animations
      </h1>
    </div>
    <headerPage v-bind:style="{ opacity: this.nums }" class="sss"></headerPage>
    <RouterView> </RouterView>
  </div>
</template>

<script>
import headerPage from "./components/header-page.vue";

export default {
  name: "App",
  components: {
    headerPage,
  },
  props: {},
  data() {
    return {
      nums: 1,
    };
  },
  methods: {
    wheel: function (ev) {
      if (ev.deltaY < 0) {
        this.nums = 1;
      } else {
        this.nums = 0;
      }
    },
  },
};
</script>

